  width: 300px;
  z-index: 999;
  right: 0;
  top: 7rem; }

@-webkit-keyframes toastPulse {
  0% {
    transform: scale(0);
    opacity: 1; }
  30% {
    opacity: 1; }
  100% {
    transform: scale(1);
    opacity: 0; } }

@keyframes toastPulse {
  0% {
    transform: scale(0);
    opacity: 1; }
  30% {
    opacity: 1; }
  100% {
    transform: scale(1);
    opacity: 0; } }

.Toastify__toast:before {
  background-size: 80%;
  content: ' ';
  width: 40px;
  height: 40px;
  position: absolute;
  left: 11px;
  z-index: 1;
  top: calc(50% - 20px);
  border-radius: 50%;
  padding: 5px 10px;
  background-position: center;
  background-repeat: no-repeat; }

.Toastify__toast:after {
  content: '';
  width: 50px;
  height: 50px;
  position: absolute;
  left: 6px;
  top: calc(50% - 25px);
  border-radius: 50%;
  padding: 5px 10px;
  z-index: 0;
  -webkit-animation: toastPulse 1.5s infinite ease;
          animation: toastPulse 1.5s infinite ease; }

.Toastify__toast-body:before {
  content: ' ';
  border-style: solid;
  border-width: 10px 15px 10px 0;
  position: absolute;
  left: -10px;
  top: calc(50% - 10px);
  height: 20px; }

